body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #a7c5eb;
  color: #413c69;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

form {
  font-family: sans-serif;
  margin-bottom: 30px;
  margin-top: 20px; 
}

label {
  display: block;
  font-size: 20px;
  margin: 10px 0; 
}

input { 
  display: block;
  width: 45%;
  height: 25px;
  border: none;
}

.container {
  width: 80%;
  margin: 0 auto;
  padding: 30px;
}

.header {
  margin: 0;
  text-align: center;
  background: #4a47a3;
  color: #fff;
  width: 100%; 
  padding: 20px 0;
  font-size: 30px;
  text-transform: uppercase;
}

button {
  margin-top: 20px;
  padding: 10px 20px;
  border: none;
  font-size: 18px;
  cursor: pointer;
  outline: none;
}


#edu div,
#exps div,
#info div {
  display: flex;
  justify-content: space-between; 
  width: 30%; 
  font-size: 18px; 
  text-transform: capitalize; 
  font-weight: bold;
}

span {
  font-weight: normal;
}

h3 {
  font-size: 25px; 
  margin-bottom: 10px; 
}
